import React, {useState} from 'react'
import {Box, Paper, TextField, Typography, Button} from '@mui/material'
import { useNavigate, Link } from 'react-router-dom'
import {useDispatch, useSelector} from 'react-redux'
import { createUser} from '../../features/userAuth/userThunk'
import {logIn} from '../../features/userAuth/userAuth'



const Register = () => {
const dispatch = useDispatch()
const navigate= useNavigate()
const [details, setDetails] = useState({FirstName:'', LastName:'', Email:'', Password:"",ConfirmPassword:'',})
const handleSubmit = (e) => {
e.preventDefault()
console.log(details)
if (details.Password !== details.ConfirmPassword)  return console.log('passwords do not match')
dispatch(createUser(details))
dispatch(logIn())
navigate('/')

  }
  return (
    <Paper elevation={2} sx={{margin:'0 auto',marginTop:'2rem', width: '300px', padding:'1rem',}}>
      
      <Box 
      component='form'
     noValidate
      autoComplete='off'
      onSubmit={handleSubmit}
        sx={{display:'flex', flexDirection:'column', gap:'0.45rem', justifyContent: 'center'}}
      >
        <Typography variant="h6" component='h1' sx={{margin:"0.2rem auto"}}>Create an Account</Typography>
       <TextField 
       type='text'
       name='firstName'
       
       onChange={(e) => setDetails({...details, FirstName: e.target.value})}
       value={details.FirstName}
       label= 'FirstName' 
       placeholder='Enter firstName'
       sx={{width:'100%'}}
       />
         <TextField 
       type='text'
       name='LastName'
       onChange={(e) => setDetails({...details, LastName: e.target.value})}
         value={details.LastName}
       label= 'LastName' 
       placeholder='Enter lastName...'
       sx={{width:'100%'}}
       />
        
         <TextField 
       type='email'
       name='Email'
         value={details.Email}
         onChange={(e) => setDetails({...details, Email: e.target.value})}
       label= 'Email' 
       placeholder='Enter email...'
       sx={{width:'100%'}}
       />
         <TextField 
       type='password'
       name='Password'
      value={details.Password}
      onChange={(e) => setDetails({...details, Password: e.target.value})}
       label= 'Password' 
       placeholder='Enter password...'
       sx={{width:'100%'}}
       />
        <TextField 
       type='password'
       name='ConformPassword'
       value={details.ConfirmPassword}
       onChange={(e) => setDetails({...details, ConfirmPassword: e.target.value})}
       label= 'Confirm Password' 
       placeholder='Enter password again...'
       sx={{width:'100%'}}
       />
       <Button type='submit' variant='contained' color='primary'>create account</Button>
      
      </Box>
      <div>
        <p>Already have an account? <Link to='/login'>sign in</Link></p>
      </div>
    </Paper>
  )
}

export default Register